.search {
    @apply relative;
}

.search__icon {
    @apply w-5 h-5 fill-neutral-500 absolute top-[25%] left-3;
}

.search__input {
    @apply rounded-xl py-2 pl-10 pr-4 bg-neutral-900 max-w-[50vw] sm:max-w-max;
}

.search__input::placeholder {
    @apply text-neutral-500;
}

.search__input:focus, .search__input:focus-visible {
    @apply outline-none;
}